  background: #e2ebf1; }

.wrapper {
  max-width: 1435px;
  margin: auto;
  background: #fff;
  padding-bottom: 65px; }

img {
  max-width: 100%;
  height: auto; }

.header {
  background: #3e4146;
  color: #fff; }

.top-panel {
  padding: 10px 0; }

