  font-size: 13.5px;
  font-family: "Lato-Medium"; }
  .top-menu li {
    display: inline-block; }
    .top-menu li + li {
      margin-left: 40px; }
  .top-menu a {
    color: #fff; }
    .top-menu a:hover {
      color: #db1551; }

.time {
  opacity: 0.66;
  font-size: 14px; }

