  color: #db1551; }

.header-center {
  padding: 30px 0; }

.search {
  position: relative;
  width: 440px; }
  .search__field {
    height: 35px;
    display: block;
    width: 100%;
    background: none;
    border: 1px solid #fefefe;
    border-radius: 50px;
    padding-left: 30px;
    padding-right: 80px;
    outline: 0;
    font-family: "Lato-Regular";
    color: #fff; }
  .search__btn {
    position: absolute;
    z-index: 2;
